// material
import {
  Table,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
  TableContainer,
  Link,
  Button,
  Stack
} from '@material-ui/core';
// components
import Scrollbar from '../Scrollbar';

// ----------------------------------------------------------------------

function createDistributionData(distDate, amount, action) {
  return { distDate, amount, action };
}

const BASIC_TABLE = [
  createDistributionData('01/11/2021', 100, 'actionCommand'),
  createDistributionData('01/11/2021', 100, 'actionCommand'),
  createDistributionData('01/11/2021', 100, 'actionCommand'),
  createDistributionData('01/11/2021', 100, 'actionCommand'),
  createDistributionData('01/11/2021', 100, 'actionCommand')
];

// ----------------------------------------------------------------------

export default function LeaderboardClaimTable() {
  return (
    <Scrollbar>
      <TableContainer sx={{ minWidth: 400, mt: 1 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell align="left">Date</TableCell>

              <TableCell align="right">Amount</TableCell>

              <TableCell align="right">Claim</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {BASIC_TABLE.map((row, index) => (
              <TableRow key={row.name}>
                <TableCell align="left">{row.distDate}</TableCell>

                <TableCell align="right">
                  <Stack direction="row" justifyContent="right">
                    {row.amount} BNB
                    <img src="/static/icons/ic_bnb.svg" style={{ width: '1rem', marginLeft: '0.5rem' }} alt="" />
                  </Stack>
                </TableCell>

                <TableCell align="right">
                  <Button variant="outlined" color="primary">
                    Claim
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Scrollbar>
  );
}
