import { useState, useEffect } from 'react';

// material
import { Box, Container, Stack, Typography, styled, Paper, Grid, LinearProgress } from '@material-ui/core';
import { motion } from 'framer-motion';
import { useTheme } from '@material-ui/core/styles';
import Lottie from 'react-lottie';

import useCountdown from '../../hooks/useCountdown';

import PotLottie from '../../assets/leaderboarddrop/watersplash.json';

const CountdownStyle = styled('div')({
  display: 'flex',
  justifyContent: 'left'
});

const SeparatorStyle = styled(Typography)(({ theme }) => ({
  margin: theme.spacing(0, 1),
  [theme.breakpoints.up('sm')]: {
    margin: theme.spacing(0, 1.5)
  }
}));

const potOptions = {
  loop: true,
  autoplay: true,
  animationData: PotLottie,
  background: 'transparent'
};

Date.prototype.addHours = function (h) {
  this.setHours(this.getHours() + h);
  return this;
};

const startCountdown = new Date().addHours(-23);
const finishCountdown = new Date().addHours(2);

export default function LeaderboardCDROPPot() {
  const countdown = useCountdown(finishCountdown);

  let progressPCT = 0;
  // if (new Date().getTime() <= finishCountdown.getTime() && timeLock !== 0) {
  if (new Date().getTime() <= finishCountdown.getTime()) {
    progressPCT = ((Date.now() - startCountdown) / (finishCountdown - startCountdown)) * 100;
  } else {
    progressPCT = 0;
  }

  return (
    <>
      <Stack direction="row">
        <Stack direction="column" style={{ width: '70%' }}>
          <Typography variant="h3">JUICE Pot</Typography>
          {/* <Typography paragraph>
                        The Top 16 most wins in 24 hours will evenly win the Leaderboard Pot prize.
                    </Typography> */}

          <Typography variant="h5" style={{ marginTop: '1rem' }}>
            Next Drop:
          </Typography>
          <CountdownStyle>
            <div>
              <Typography variant="h6">{countdown.hours}</Typography>
              <Typography sx={{ color: 'white', opacity: '0.5', marginTop: '-0.2rem' }}>Hours</Typography>
            </div>

            <SeparatorStyle variant="h6">:</SeparatorStyle>

            <div>
              <Typography variant="h6">{countdown.minutes}</Typography>
              <Typography sx={{ color: 'white', opacity: '0.5', marginTop: '-0.2rem' }}>Minutes</Typography>
            </div>

            <SeparatorStyle variant="h6">:</SeparatorStyle>

            <div>
              <Typography variant="h6">{countdown.seconds}</Typography>
              <Typography sx={{ color: 'white', opacity: '0.5', marginTop: '-0.2rem' }}>Seconds</Typography>
            </div>
          </CountdownStyle>

          <Box sx={{ width: '90%', marginTop: '0.5rem' }}>
            <LinearProgress variant="determinate" color="primary" value={progressPCT} />
          </Box>
        </Stack>

        <Stack
          style={{ width: '30%', marginLeft: 'auto', textAlign: 'center', position: 'relative' }}
          justifyContent="center"
        >
          <object
            type="image/svg+xml"
            data="/static/leaderboarddrop/JUICE-blinking.svg"
            style={{ width: '100%', zIndex: '2', position: 'absolute' }}
          >
            l
          </object>
          {/* <Lottie
                        options={potOptions}
                        style={{ width: '100%', margin: '0', zIndex: '3'}}
                        className="watersplash-lottie"
                        isClickToPauseDisabled
                    /> */}
        </Stack>
      </Stack>

      <Stack style={{ textAlign: 'end' }}>
        <Typography variant="h5" style={{ lineHeight: '1', marginTop: '1.5rem' }}>
          Total Drop
        </Typography>

        <Stack direction="row" alignItems="center" justifyContent="end">
          <Typography variant="h3" style={{ color: '#35C4EF' }}>
            100.000.000.000 JUICE
          </Typography>
          <img alt="" src="/static/icons/ic_JUICE.svg" style={{ width: '1.8rem', marginLeft: '0.5rem' }} />
        </Stack>
      </Stack>
    </>
  );
}
